import moment from "moment";
import AuthContentSideInfoCSS from "./AuthContentSideInfo.module.css";

export default function AuthContentSideInfo() {

    const today = moment(new Date()).format("YYYY MM DD");

    return (
        <div className={ AuthContentSideInfoCSS.sideArea }>
            <div className={ AuthContentSideInfoCSS.date }>{ today }</div>
            <div className={ AuthContentSideInfoCSS.authStatusText }><span className={ AuthContentSideInfoCSS.text }>  </span><span className={ AuthContentSideInfoCSS.count }>4</span><span className={ AuthContentSideInfoCSS.text }>  !</span></div>
            <div className={ AuthContentSideInfoCSS.authStatus }><label className={ AuthContentSideInfoCSS.text }> : </label><span className={ AuthContentSideInfoCSS.count }>1</span><span className={ AuthContentSideInfoCSS.text }> </span></div>
            <div><label className={ AuthContentSideInfoCSS.text }> : </label><span className={ AuthContentSideInfoCSS.count }>1</span><span className={ AuthContentSideInfoCSS.text }> </span></div>
        </div>
    );
}