import axios from 'axios';
import { useEffect, useRef, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import { callGetChallengeInfoAPI, modifyChallengeAPI, registChallengeImagesAPI } from '../../../apis/ChallengeAPICalls';
import ModifyAttachments from '../../../components/challenge/modify/ModifyAttachments';
import JoinAmount from '../../../components/challenge/regist/JoinAmount';
import { PUT_END_TIME, PUT_START_TIME, PUT_CHALLENGE_DESCRIPTION, PUT_CHALLENGE_INFO, PUT_CHALLENGE_CATEGORY_NO, SET_MODIFY_ATTACHMENT } from '../../../modules/ChallengesModule';
import AuthDay from './AuthDay';
import ModifyChallengeCSS from './ModifyChallenge.module.css';

function ModifyChallenge() {
    
  const { challengeNo } = useParams();
    
  const { challengeInfo, authDayInfo, attachmentInfo, modifyAttachment } =  useSelector(state => state.challengesReducer);
  const navigate = useNavigate(); 

  const dispatch = useDispatch();
  
  const imageInput1 = useRef();
  const imageInput2 = useRef();
  const imageInput3 = useRef();
  const imageInput4 = useRef();

  const modifyHandler = async () => {

    for(let i = 0; i < modifyAttachment.length; i++) {

      if(modifyAttachment[i].path) {
      const result = await registChallengeImagesAPI({inputFile: modifyAttachment[i].formData, index: i});

      dispatch({type: SET_MODIFY_ATTACHMENT, payload: {data: result, index: i}});
      }
    }


    await modifyChallengeAPI({challenge: challengeInfo, attachments: modifyAttachment});

     await alert('  !');
     await window.location.replace(`/challenges/${challengeNo}`);
  };

  
  
  useEffect(
    () => {
      dispatch(callGetChallengeInfoAPI(challengeNo));
    },
    []
  );

  const categoryOnClickHandler = (e) => {
    dispatch({type: PUT_CHALLENGE_CATEGORY_NO, payload: e.target.value});
  };

  const descriptionOnChangeHandler = (e) => {
    dispatch({type: PUT_CHALLENGE_DESCRIPTION, payload: e.target.value});
  };

  const startTimeChangeHandler = (e) => {
    dispatch({type: PUT_START_TIME, payload: e.target.value});
  };
  
  const endTimeChangeHandler = (e) => {
    dispatch({type: PUT_END_TIME, payload: e.target.value});
  };
  
  const infoChangeHandler = (e) => {
    dispatch({type: PUT_CHALLENGE_INFO, payload: e.target.value});
  };

  const scopeOnChangeHandler = (e) => { };
  return (
    <div className={ ModifyChallengeCSS.area }>
      <div className={ ModifyChallengeCSS.head }>
        <h1> </h1>
        <label>
            .    , ,      .
        </label>
        <hr/>
      </div>
      <br/>
      <br/>
      <div className={ ModifyChallengeCSS.btnarea }>
        <button onClick={ categoryOnClickHandler } value='1' className={ challengeInfo.challengeCategoryNo == '1'? ModifyChallengeCSS.choosenbtn: ModifyChallengeCSS.defaultbtn }></button>
        <button onClick={ categoryOnClickHandler } value='2' className={ challengeInfo.challengeCategoryNo == '2'? ModifyChallengeCSS.choosenbtn: ModifyChallengeCSS.defaultbtn }></button>
        <button onClick={ categoryOnClickHandler } value='3' className={ challengeInfo.challengeCategoryNo == '3'? ModifyChallengeCSS.choosenbtn: ModifyChallengeCSS.defaultbtn }></button>
        <button onClick={ categoryOnClickHandler } value='4' className={ challengeInfo.challengeCategoryNo == '4'? ModifyChallengeCSS.choosenbtn: ModifyChallengeCSS.defaultbtn }></button>
        <button onClick={ categoryOnClickHandler } value='5' className={ challengeInfo.challengeCategoryNo == '5'? ModifyChallengeCSS.choosenbtn: ModifyChallengeCSS.defaultbtn }></button>
      </div>
      <div >
        <label className={ ModifyChallengeCSS.subtitle }> </label><br/>
        <label className={ ModifyChallengeCSS.description }>          .</label><br/>
        <input className={ ModifyChallengeCSS.textinput} value={ challengeInfo.challengeName } readOnly/><br/>
      </div>
      <div>
        <label className={ ModifyChallengeCSS.subtitle }> </label><br/>
        <label className={ ModifyChallengeCSS.description }>     .</label><br/>
        <textarea className={ ModifyChallengeCSS.authDescription} value={ challengeInfo.challengeAuthExplan } 
          onChange={ descriptionOnChangeHandler }/><br/> 
      </div>
      <div className={ ModifyChallengeCSS.checkinput}>
        <label className={ ModifyChallengeCSS.subtitle }> </label><br/>
        <JoinAmount amount={ challengeInfo.challengeMaxAmount }/>
      </div>
      
      
      <div className={ ModifyChallengeCSS.checkinput}>
        <label className={ ModifyChallengeCSS.subtitle }> </label><br/>
        <div>
          <input readOnly id="freq-1" type="radio" value='1' checked={challengeInfo.challengeFrequency == '1'}/><label htmlFor='freq-1'> </label>
          <input readOnly id="freq-2" type="radio" value='2' checked={challengeInfo.challengeFrequency == '2'}/><label htmlFor='freq-2'> 3 </label>
          <input readOnly id="freq-3" type="radio" value='3' checked={challengeInfo.challengeFrequency == '3'}/><label htmlFor='freq-3'> 1 </label>
        </div>
        <div>
          <AuthDay freq={challengeInfo.challengeFrequency} authDays={authDayInfo} />
        </div> 
      </div>


      <div className={ ModifyChallengeCSS.timeinput}>
        <label className={ ModifyChallengeCSS.subtitle }>  </label><br/>
        <div>
          <label> <input type="time" onChange={ startTimeChangeHandler } value={ challengeInfo.startTime}/></label>
          <br/><br/>
          <label> <input type="time" onChange={ endTimeChangeHandler } value={ challengeInfo.endTime}/></label>
        </div>
      </div>


        <div className={ ModifyChallengeCSS.timeinput}>
          <label className={ ModifyChallengeCSS.subtitle }> </label><br/>
          <div>
            <input readOnly value={ challengeInfo.challengeStartDate } type="date"/>
          </div>
        </div>
        <div className={ ModifyChallengeCSS.checkinput}>
          <label className={ ModifyChallengeCSS.subtitle }> </label><br/>
          <div>
            <input readOnly type="radio" id='term-1' value='1' checked={ challengeInfo.challengeTerm == '1' }/><label htmlFor='term-0'>1</label>
            <input readOnly type="radio" id='term-2' value='2' checked={ challengeInfo.challengeTerm == '2' }/><label htmlFor='term-1'>2</label>
            <input readOnly type="radio" id='term-3' value='3' checked={ challengeInfo.challengeTerm == '3' }/><label htmlFor='term-2'>3</label>
            <input readOnly type="radio" id='term-4' value='4' checked={ challengeInfo.challengeTerm == '4' }/><label htmlFor='term-3'>4</label>
          </div>
        </div>
        <div className={ ModifyChallengeCSS.checkinput}>
          <label className={ ModifyChallengeCSS.subtitle }> </label><br/>
          <div>
            <input id='public' type="radio" value='public' checked={ challengeInfo.challengeScope === 'public' } onChange={ scopeOnChangeHandler }/><label htmlFor='public'></label>
            <input id='private' type="radio" value='private' checked={ challengeInfo.challengeScope === 'private' } onChange={ scopeOnChangeHandler }/><label htmlFor='private'></label>
          </div>
        </div>
        <div className={ ModifyChallengeCSS.descriptarea}>
          <label className={ ModifyChallengeCSS.subtitle }> </label><br/>
          <textarea className={ ModifyChallengeCSS.authDescription}
           value={ challengeInfo.challengeInfo } onChange={ infoChangeHandler }/>
        </div>
        
        <ModifyAttachments index={ 1 } inputFile={imageInput1} pathInfo={ attachmentInfo[0] } modifyFile={modifyAttachment} title='  1'/>
        <ModifyAttachments index={ 2 } inputFile={imageInput2} pathInfo={ attachmentInfo[1] } modifyFile={modifyAttachment} title='  2'/>
        <ModifyAttachments index={ 3 } inputFile={imageInput3} pathInfo={ attachmentInfo[2] } modifyFile={modifyAttachment} title='  3'/>
        <ModifyAttachments index={ 4 } inputFile={imageInput4} pathInfo={ attachmentInfo[3] } modifyFile={modifyAttachment} title='  4'/>

        <div className={ ModifyChallengeCSS.modifybtnarea }>
          <button onClick={ modifyHandler }></button>
          <button></button>
        </div>
    </div>
  );
}

export default ModifyChallenge;