import { createActions, handleActions } from 'redux-actions';

/*  */
const initialState = [];

/*  */
export const GET_MENUS = 'menus/GET_MENUS';

const actions = createActions({
    [GET_MENUS]: () => {}
});

/*  */
const menuReducer = handleActions(
    {
        [GET_MENUS]: (state, { payload }) => {

            return payload;
        }
    },
    initialState
);

export default menuReducer;