import { useState } from 'react';
import LoginFormCSS from'./LoginFormCSS.module.css';
import { Link, useNavigate } from 'react-router-dom';
import { loginAPI } from '../../apis/MemberAPICalls';
import { setCookie, getCookie} from '../../cookies/cookie';

function LoginForm() {

  const navigate = useNavigate();
  const [user, setUser] = useState({
    id: '',
    password: ''
  });

  const onChangeHandler = e => {
    setUser({
      ...user,
      [e.target.name]: e.target.value
    });
  };

  const onClickHandler = async(e) => {
    const id = user.id;
    const password = user.password;

    await loginAPI(id, password)
    .then((res) => {
      console.log(res);
      const generatedToken = res.data.token
      if(generatedToken != undefined && generatedToken != null) {
        setCookie('token', generatedToken, {
          path:"/",
            secure:true,
            sameSite:'none',
        })
        navigate('/');
      } else {
        alert('   .')
      }
    })
    
  };

  return (
    <>
      <div className={LoginFormCSS.container}>
        <div className={LoginFormCSS.header}> </div>
        <div className={LoginFormCSS.content}>
          <div className={LoginFormCSS.idInput}>
            <label></label>
            <input onChange={ onChangeHandler } name="id" type="text" value={user.id} placeholder=' '/>
          </div>
          <div className={LoginFormCSS.pwdInput}>
            <label></label>
            <input onChange={ onChangeHandler } name="password" type='password' value={user.password} placeholder=' '/>
          </div>
          <div className={LoginFormCSS.loginBtnArea}>
            <button onClick={ onClickHandler } className={LoginFormCSS.loginBtn}></button>
          </div>
          <div className={LoginFormCSS.signUpArea}>
            <Link to="/sign/signup">
              <button className={LoginFormCSS.signUpBtn}></button>
            </Link>
            <Link to="/sign/id">
              <button className={LoginFormCSS.findIdBtn}> </button>
            </Link>
          </div> 
        </div>
        
      </div>
    </>
  );
}

export default LoginForm;